import React from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import projects from '../../projects/index';

function Portfolio () {
   return (
      <section id="_portfolio" className="portfolio">
         <div className="container panel">

            <div className="section-name">
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
               <ScrollAnimation animateIn="open-anim" animateOut="close-anim" offset={100}>
                  <h3>PORTFOLIO</h3>
               </ScrollAnimation>
               <ScrollAnimation animateIn="fade-in-anim" animateOut="fade-out-anim" offset={100}>
                  <div className="bar"></div>
               </ScrollAnimation>
            </div>

            <ul className="projects">
            { projects.map((e, i) => (
               <li className={`project-${i+1}`}>
                  <a href={ e.link }>
                    <div className="thumb" style={{background:`url(${e.images.thumb}) 0% 0% / contain`}}></div>
                  </a>
                  <a className="title" href={ e.link }>{ e.title }</a>
                  <div>
                    <a className="github" href={ e.github }>Github</a>
                    { e.designs && <div className="divider"></div> }
                    { e.designs && <a className="designs" href={ e.designs }>Designs</a> }
                  </div>
               </li>
            )) }
            </ul>

         </div>
      </section>
   );
}

export default Portfolio;


